import React from 'react';
import { Route, BrowserRouter } from 'react-router-dom';
import StartPage from './components/Pages/StartPage.js';
import MoviesPage from './components/Pages/MoviesPage.js';
import NewSeason from './components/Pages/NewSeason.js';
import PopularPage from './components/Pages/PopularPage.js';
import DetailsPage from './components/Pages/DetailsPage.js';
import SearchPage from './components/Pages/SearchPage.js';
import RecentDUB from './components/Pages/RecentDUB';

import './App.css';

function App() {
  return (
    <div>
      <BrowserRouter basename={process.env.PUBLIC_URL}>
        <Route path={["/", "/page=:page"]}  exact render={(props) => <StartPage key={props.location.key} /> }  />
        <Route path={["/movies", "/movies/page=:page"]} exact render={(props) => <MoviesPage key={props.location.key} />} />
        <Route path={["/new-season", "/new-season/page=:page"]} exact render={(props) => <NewSeason key={props.location.key} />} />
        <Route path={["/popular", "/popular/page=:page"]} exact render={(props) => <PopularPage key={props.location.key} />} />
        <Route path="/videos/:video" exact render={(props) => <DetailsPage key={props.location.key} />} />
        <Route path={["/recent-dub", "/recent-dub/page=:page"]} exact render={(props) => <RecentDUB key={props.location.key} />} />
        <Route path={["/search", "/search/keyword=:keyword/page=:page"]} exact render={(props) => <SearchPage key={props.location.key} />}/>
      </BrowserRouter>
    </div>
  );
}

export default App;
