import React, { useState } from 'react';
import { makeStyles, fade } from '@material-ui/core/styles'
import { 
  AppBar, 
  Toolbar, 
  Typography, 
  IconButton, 
  Menu, 
  MenuItem,
  TextField,
} from '@material-ui/core';
import { Search as SearchIcon, MoreVert as MoreIcon } from '@material-ui/icons';
import { Link } from 'react-router-dom';
import SideDrawer from './SideDrawer.js';

const useStyles = makeStyles((theme) => ({
  grow: {
    flexGrow: 1,
  },
  title: {
    display: 'none',
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
  },
  search: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    marginRight: theme.spacing(2),
    marginLeft: 0,
    width: 'auto',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing(3),
      width: 'auto',
    },
  },
  searchIcon: {
    padding: theme.spacing(0, 2),
    height: '100%',
    position: 'absolute',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRoot: {
    color: 'inherit',
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('md')]: {
      width: '20ch',
    },
  },
  sectionDesktop: {
    display: 'none',
    [theme.breakpoints.up('md')]: {
      display: 'flex',
    },
  },
  sectionMobile: {
    display: 'flex',
    [theme.breakpoints.up('md')]: {
      display: 'none',
    },
  },
}));

const Head = (props) => {
  const classes = useStyles();
  const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = useState(null);


  const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);

  const handleMobileMenuClose = () => {
    setMobileMoreAnchorEl(null);
  };

  // const handleMenuClose = () => {
  //   setAnchorEl(null);
  //   handleMobileMenuClose();
  // };

  const handleMobileMenuOpen = (event) => {
    setMobileMoreAnchorEl(event.currentTarget);
  };

  const mobileMenuId = 'primary-search-account-menu-mobile';
  const renderMobileMenu = (
    <Menu
      anchorEl={mobileMoreAnchorEl}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      id={mobileMenuId}
      keepMounted
      transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      open={isMobileMenuOpen}
      onClose={handleMobileMenuClose}
    >
      <MenuItem selected={(window.location.pathname === "/" | window.location.pathname.startsWith('/page')) ? true : false}>
        <Link to="/">
          <p>SUB Animes</p>
        </Link>
      </MenuItem>
      <MenuItem selected={(window.location.pathname === "/recent-dub") ? true : false} >
        <Link to="/recent-dub">
        <p>DUB Animes</p>
        </Link>
      </MenuItem>
      
    </Menu>
  );

  const handleSearch = () => {
    if (window.location.pathname.startsWith("/search")) {
      return (
        <TextField 
          label="Search"
          InputProps={{
            endAdornment: (
              <IconButton onClick={() => console.log('clicked')}>
                <SearchIcon />
              </IconButton>
            )
          }}
          value={props.value}
          onChange={props.onChangeValue}
          size="small"
          variant="filled"
          color="secondary"
          onKeyDown={(e) => {
            if (e.key === "Enter") {
              console.log('clicked Enter')
            }
          }}
        />
      )
    } else {
      return (
        <Link to="/search">
          <TextField 
            label="Search Tap twice"
            InputProps={{
              endAdornment: (
                <IconButton onClick={() => console.log('clicked')}>
                  <SearchIcon />
                </IconButton>
              )
            }}
            value={props.value}
            onChange={props.onChangeValue}
            size="small"
            variant="filled"
            color="secondary"
            onKeyDown={(e) => {
              if (e.key === "Enter") {
                console.log('clicked Enter')
              }
            }}
          />
        </Link>
      )
    }
  }
  return (
    <div className={classes.grow}>
      <AppBar position="static" color="transparent">
        <Toolbar>
          <SideDrawer />
          <Typography className={classes.title} variant="h5" noWrap>
            <Link to="/">
              Gogoanime
            </Link>
          </Typography>
          <div className={classes.search}>
            {handleSearch()}
          </div>
          <div className={classes.grow} />
          <div className={classes.sectionDesktop}>
            <IconButton >
              <Link to="/">
                <span style={{ fontSize: '18px', color: (window.location.pathname === '/' | window.location.pathname.startsWith("/page")) ? "#f44336" : "white" }}>
                  SUB
                </span>
              </Link>
            </IconButton>
            <IconButton >
              <Link to="/recent-dub">
                <span 
                  style={{ fontSize: '18px', color: (window.location.pathname.startsWith('/recent-dub')) ? "#f44336" : "white" }}>
                  DUB
                </span>
              </Link>
            </IconButton>
            
          </div>
          <div className={classes.sectionMobile}>
            <IconButton
              aria-label="show more"
              aria-controls={mobileMenuId}
              aria-haspopup="true"
              onClick={handleMobileMenuOpen}
              color="inherit"
            >
              <MoreIcon />
            </IconButton>
          </div>
        </Toolbar>
      </AppBar>
      {renderMobileMenu}
    </div>
  );
}

export default Head;