import React, { useState } from 'react';
import { AppBar, Toolbar, Typography, IconButton, Menu, MenuItem } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { MoreVert as MoreIcon } from '@material-ui/icons';
import { Link } from 'react-router-dom';
import './NavHead.css';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1
  },
  categories: {
    flexGrow: 1
  },
  showLogo: {
    display: 'none',
    [theme.breakpoints.down('sm')]: {
      display: 'flex'
    },
    flexGrow: 1
  },
  sectionDesktop: {
    display: 'none',
    [theme.breakpoints.up('sm')]: {
      display: 'flex',
    },
    flexGrow: 1
  },
  sectionMobile: {
    display: 'flex',
    [theme.breakpoints.up('sm')]: {
      display: 'none',
    },
  },
}));

const NavHead = () => {
  const classes = useStyles();

  const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = useState(null);

  const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);
  
  // useEffect(() => {

  // }, [window.location.pathname]);

  const handleMobileMenuClose = () => {
    setMobileMoreAnchorEl(null);
  };

  const handleMobileMenuOpen = (event) => {
    setMobileMoreAnchorEl(event.currentTarget);
  };

  const mobileMenuId = 'mobile-view';
  const renderMobileMenu = (
    <Menu
      anchorEl={mobileMoreAnchorEl}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      id={mobileMenuId}
      keepMounted
      transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      open={isMobileMenuOpen}
      onClose={handleMobileMenuClose}
    >
      <MenuItem selected={(window.location.pathname === "/" | window.location.pathname.startsWith('/page')) ? true : false}>
        <Link to="/">
          <p>Recent Release</p>
        </Link>
      </MenuItem>
      <MenuItem selected={(window.location.pathname === "/movies") ? true : false}>
        <Link to="/movies">
          <p>Movies</p>
        </Link>
      </MenuItem>
      <MenuItem selected={(window.location.pathname === "/new-season") ? true : false}>
        <Link to="/new-season">
          <p>New Season</p>
        </Link>
      </MenuItem>
      <MenuItem selected={(window.location.pathname === "/popular") ? true : false}>
        <Link to="/popular">
          <p>Popular Anime</p>
        </Link>
      </MenuItem>
    </Menu>
  );

  return (
    <div className={classes.root}>
      <AppBar position="static" color="inherit">
        <Toolbar>
          <Typography className={classes.showLogo}>
            Gogoanime
          </Typography>
          <div className={classes.sectionDesktop}>
            <Typography className={classes.categories}>
              <Link to="/" >
                <span style={{ color: (window.location.pathname === "/" | window.location.pathname.startsWith('/page')) ? "#f44336" : "white" }}>
                  Recent Release
                </span>
              </Link>
            </Typography>
            <Typography className={classes.categories}  >
              <Link to="/movies" >
                <span style={{ color: window.location.pathname.startsWith('/movies') ? "#f44336" : "white" }}>
                  Movies
                </span>
              </Link>
            </Typography>
            <Typography className={classes.categories}  >
              <Link to="/new-season" >
                <span style={{ color: window.location.pathname.startsWith('/new-season') ? "#f44336" : "white" }}>
                  New Season
                </span>
              </Link>
            </Typography>
            <Typography className={classes.categories}  >
              <Link to="/popular" >
                <span style={{ color: window.location.pathname.startsWith('/popular') ? "#f44336" : "white" }}>
                  Popular Anime
                </span>
              </Link>
            </Typography>
          </div>
          <div className={classes.sectionMobile}>
            <IconButton
              aria-label="show more"
              aria-controls={mobileMenuId}
              aria-haspopup="true"
              onClick={handleMobileMenuOpen}
              color="inherit"
            >
              <MoreIcon />
            </IconButton>
          </div>
        </Toolbar>
      </AppBar>
      {renderMobileMenu}
    </div>
  );
}

export default NavHead;